<template>
     <li class="catalog__item">
        <router-link class="catalog__pic" :to="{name:'product', params:{id: product.id}}">
              <img :src="product.image"  :alt="product.title">
        </router-link>

            <h3 class="catalog__title">
              <router-link class="" :to="{name:'product', params:{id: product.id}}">

                {{ product.title }}
              </router-link>
            </h3>

            <span class="catalog__price">

              {{ product.price | numberFormat}} ₽
            </span>
            <ul class="colors colors--black">
              <li class="colors__item" v-for="color in product.colors" :key="color.id">
                <label class="colors__label">
                  <input class="colors__radio sr-only" type="radio" name="color" v-model="currentColor" >
                  <span class="colors__value" v-bind:style="{ backgroundColor: color.code }">
                  </span>
                </label>
              </li>
            </ul>
        </li>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';

export default{
    props: ['product'],
    filters: {
        numberFormat
    },
    computed: {
      currentColor(){
        return this.product.colors[0].code
      }
    },
}
</script>