<template>
    <main class="content container">
      <div class="content__top content__top--catalog">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          152 товара
        </span>
      </div>
      <div class="content__catalog">

        <ProductFilter :price-form.sync="filterPriceForm" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId" />
        <section class="catalog">
         <load-spinner v-if="productsLoading"/>
         <div v-else-if="productsLoadingFailed">Произошла ошибка при загрузке товаров <button @click.prevent="loadProducts">Попробовать еще раз</button></div>
          <ProductList v-else :products="products"/>
          <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
        </section>
      </div>
    </main>
</template>
<script>
import BasePagination from '@/components/BasePagination.vue';
import LoadSpinner from '@/components/LoadSpinner.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import ProductList from '@/components/ProductList.vue';
import axios from 'axios';
import { API_BASE_URL } from "../config";
export default {
    components: { ProductList, BasePagination, ProductFilter, LoadSpinner },
    data() {
        return {
          filterPriceForm:0,
          filterPriceTo:0,
          filterCategoryId:0,
          filterColorId:0,
          page: 1,
          productsPerPage: 3,
          productsData: null,
          productsLoading:false,
          productsLoadingFailed: false
        }
    },
    computed:{
      products(){
        return this.productsData
        ? this.productsData.items.map(product => {
          return{
            ...product,
            image: product.image.file.url
          }
        })
        : [];
      },
      countProducts(){
        return this.productsData ? this.productsData.pagination.total : 0;
      }
    },
    methods:{
      loadProducts(){
        this.productsLoading = true;
        this.productsLoadingFailed = false;
        clearTimeout(this.loadProductsTimer)
        this.loadProductsTimer = setTimeout(() => {
          axios
            .get(API_BASE_URL + '/api/products',{
              params:{
                page: this.page,
                limit: this.productsPerPage,
                categoryId: this.filterCategoryId,
                colorId: this.filterColorId,
                minPrice: this.filterPriceForm,
                maxPrice: this.filterPriceTo
              }
            })
            .then(response => this.productsData = response.data)
            .catch(() => this.productsLoadingFailed = true)
            .then(() => this.productsLoading = false);
        },0)
      }
    },
    watch:{
      page(){
        this.loadProducts();
      },
      filterPriceForm(){
        this.loadProducts();
      },
      filterPriceTo(){
        this.loadProducts();
      },
      filterCategoryId(){
        this.loadProducts();
      },
      filterColorId(){
        this.loadProducts();
      }
    },
    created(){
      this.loadProducts();
    }
}
</script>