<template>
<ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled' :  prevIsDisabled}" aria-label="Предыдущая страница" @click.prevent="paginate(currentPage-1)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </a>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a class="pagination__link" :class="{'pagination__link--current':pageNumber === page}" @click.prevent="paginate(pageNumber)">
              {{pageNumber}}
            </a>
          </li>

          <li class="pagination__item">
            <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled' :  nextIsDisabled}" href="#" aria-label="Следующая страница" @click.prevent="paginate(currentPage+1)">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </a>
          </li>
        </ul>

</template>

<script>
//import ProductItem from './ProductItem.vue';
export default{
  model:{
    prop:'page',
    event:'paginate',
  },
  props:['page', 'count', 'perPage'],
  computed:{
      pages(){
          return Math.ceil(this.count / this.perPage);
      },
      currentPage(){
        return this.page
      },
      prevIsDisabled(){
        return (this.currentPage== 1) ? true : false;
      },
      nextIsDisabled(){
        return (this.currentPage == this.pages) ? true : false;
      },

  },
  methods:{
    paginate(page){
      this.$emit('paginate', page);
    },
  }
}
</script>